<!-- 功能授权详情 -->
<template>
	 <div class="action-detail">
		 <div class="detail-header">
			 <base-title bread title="功能授权" :breadList="['系统管理员']">
				<template slot="btn">
					<btn-list @btn-click="saveAdd" :model="{icon:'icon-save',name:'保存'}"></btn-list>
				</template>
			 </base-title>
		 </div>
		 <div class="detail-content">
			 <div class="left">
				<div class="menu">
                    <el-tree
                            ref="treeLeft"
                            :data="allText"
                            show-checkbox
                            node-key="name"
                            @check-change="leftLeCheckChange"
                            @node-click="leftHandleCheckChange"
                            :props="defaultProps">
                    </el-tree>
				</div>
			 </div>
			 <div class="right">
				<el-tree
                     ref="tree"
					:data="rntData"
					show-checkbox
					node-key="name"
                    @check-change="handleCheckChange"
					:props="defaultProps">
				</el-tree>
			 </div>
		 </div>
	 </div>
</template>

<script>
export default {
    components:{
        baseTitle:()=>import("@/components/title"),
        btnList:()=>import("@/components/btn-list"),
    },
    data(){
        return {
            parId:'',
            checkList:[],
            number:0,
            projectData:[],
            parameter:[],
            checked:false,
            childData:'',
            defaultProps: {
                children: 'children',
                label: 'desc'
            },
            leftNames:[], // 左边所有的name
            rightHavePermission: [], // 右边有权限的name
            leftClickTitle:[],
            currentNodekey:'',
            rntData: [],
            leftHavePermission: [], // 左边有权限的name
            allData: {}, // 左边每种类型的name，对应它右边的树的数据
            allText:[],// 左边的text
            nameType: {}, // 右边的树里面的数据的name，属于左边的哪种类型的name
            havePermissionNames: {},
            currentLeftName: "", // 当前左边焦点的name
        }
    },
    //初始化
    created() {
        this.parId = this.$route.query.id;
        this.leftList();
    },
    methods:{
        //所有权限
        leftList(){
            let parameter = {};
            this.$api.getPermissionTree(parameter).then(res => {
                if(res.success == true){
                    let _this = this;
                    res.data.forEach(item => {
                        _this.allData[item.name] = {
                            "children": item.children,
                        };
                        _this.forEachChildren(item);
                        let name = {
                            desc:item.desc,
                            name:item.name
                        };
                        _this.allText.push(name);
                    });
                    this.currentLeftName = res.data[0].name;
                    _this.showRightTree();
                    this.getPermissionByRoleId();
                }
            })
        },
        forEachChildren(item) {
            let _this = this;
            if (item.children.length > 0) {
                item.children.forEach(child => {
                    _this.nameType[child.name] = child.rootName;
                    _this.forEachChildren(child);
                });
            }
        },
        //根据角色id查询权限
        getPermissionByRoleId(){
            let parameter = {
                roleId:this.parId,
                orgId:'1'
            };
            this.$api.getPermissionByRoleId(parameter).then(res => {
                if(res.success == true){
                    let _this = this;
                    res.data.forEach(item => {
                        if (_this.nameType.hasOwnProperty(item.name)) {
                            let rootName = _this.nameType[item.name]
                            if (!_this.havePermissionNames.hasOwnProperty(rootName)) {
                                _this.havePermissionNames[rootName] = [];
                            }
                            _this.havePermissionNames[rootName].push(item.name);
                        }
                    });

                    let left = [];
                    for(let key in _this.havePermissionNames){
                        left.push(key);
                    }
                    _this.leftHavePermission = left;
                    this.checkRightNodes();
                    this.checkRightNodesLeft();

                }
            })
        },

        showRightTree() {
            this.rntData = this.allData[this.currentLeftName].children;
        },
        // 选中zuo边的树节点
        checkRightNodesLeft() {
            let needCheckedNames = this.leftHavePermission;
            if (needCheckedNames) {
                needCheckedNames.forEach(item => {
                    this.$refs.treeLeft.setChecked(item,true);
                });
            }
        },
        // 选中右边的树节点
        checkRightNodes() {
            let needCheckedNames = this.havePermissionNames[this.currentLeftName];
            if (needCheckedNames) {
                needCheckedNames.forEach(item => {
                    this.$refs.tree.setChecked(item,true);
                });
            }
        },
        //点击子系统
        leftHandleCheckChange(val){
            if (this.currentLeftName != "") {
                let name = this.$refs.tree.getCheckedKeys();
                this.havePermissionNames[this.currentLeftName] = name;
                let currentLeftName = [];
                currentLeftName.push(this.currentLeftName)
            }
            this.currentLeftName = val.name;
            this.showRightTree();
            this.$nextTick(function(){
                this.checkRightNodes();
            });
        },
        leftLeCheckChange(val,isCheckd){
            if(isCheckd == false){
                let name = this.havePermissionNames[val.name];
                name.forEach(item => {
                    this.$refs.tree.setChecked(item,false);

                })
                this.havePermissionNames[val.name] = [];
            }
        },
        //点击子系统的菜单
        handleCheckChange(){
            let name = this.$refs.tree.getCheckedKeys();
            if(name.length === 0){
                this.$refs.treeLeft.setChecked(this.currentLeftName,false);
            }else {
                this.$refs.treeLeft.setChecked(this.currentLeftName,true);
            }
        },
        //保存
        saveAdd(){
            let _this =this;
            if (this.currentLeftName != "") {
                let name = this.$refs.tree.getCheckedKeys();
                this.havePermissionNames[this.currentLeftName] = name;
            }
            let checkedNames = [];
            for(let key in _this.havePermissionNames){
                checkedNames = checkedNames.concat(_this.havePermissionNames[key])
            }
            let leftData =  this.$refs.treeLeft.getCheckedKeys();
            checkedNames = checkedNames.concat(leftData)
            console.log(checkedNames)
            if(checkedNames != ''){
                let parameter = {
                    name:checkedNames,
                    roleId:this.parId,
                    orgId:localStorage.getItem('orgId')
                };
                _this.$api.associatedPermission(parameter).then(res => {
                    if(res.success == true){
                        _this.$myLayer.successLayer(res.msg)
                    }
                })
            }else {
                _this.$myLayer.errorLayer('至少选择一条数据')
            }
        }
    }
}
</script>
<style>
    .el-tree-node.is-current > .el-tree-node__content {
        background-color: #F1F2F2 !important;
        color: #4A80F6 !important;
    }
</style>

<style lang="scss" scoped="scoped">
.action-detail {
	height: calc(100% - 52px);
	.detail-header {
		padding: 18px 24px 0;
	}
	.detail-content {
		height: 100%;
		margin-top: 10px;
		margin-right: 24px;
		border-top: 1px solid #E5E7EF;
		display: flex;
		.left {
			width: 210px;
			padding: 20px 0px;
			border-right: 1px solid #E5E7EF;
			.menu {
				li {
					height: 40px;
                    padding: 0px 20px;
                    line-height: 40px;
				}
			}
		}
		.right {
			flex:1;
			padding: 20px;
		}
	}
}


</style>
